"use client";

import { useRouter } from "next/navigation";
import { Plus } from "lucide-react";

import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";

// TEMP mock data (later from backend)
const documents = [
  {
    id: "1",
    title: "DSA Notes",
    updatedAt: "2 hours ago",
  },
  {
    id: "2",
    title: "ML Project Ideas",
    updatedAt: "Yesterday",
  },
];

export default function DashboardPage() {
  const router = useRouter();

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold">Your Notes</h1>
        <Button onClick={() => alert("Create note API later")}>
          <Plus className="mr-2 h-4 w-4" />
          New Note
        </Button>
      </div>

      {/* Cards Grid */}
      <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3">
        {/* Add New Note Card */}
        <Card
          className="cursor-pointer border-dashed hover:bg-muted"
          onClick={() => alert("Create note API later")}
        >
          <CardContent className="flex h-40 items-center justify-center">
            <div className="text-center text-muted-foreground">
              <Plus className="mx-auto mb-2 h-6 w-6" />
              <p>Add New Note</p>
            </div>
          </CardContent>
        </Card>

        {/* Existing Notes */}
        {documents.map((doc) => (
          <Card
            key={doc.id}
            className="cursor-pointer hover:shadow-md transition"
            onClick={() => router.push(`/dashboard/documents/${doc.id}`)}
          >
            <CardHeader>
              <CardTitle>{doc.title}</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-muted-foreground">
                Last updated {doc.updatedAt}
              </p>
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  );
}
